.masthead {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  background-color: rgba(255, 255, 255, 0.98);
  z-index: 1000;
  display: table;
}
@media all and (min-width: 768px) {
  .masthead {
    padding: 10px 15px;
  }
}
.masthead .main-header,
.masthead .main-nav {
  display: table-cell;
  vertical-align: middle;
}

.main-header a {
  color: #b144ab;
  line-height: 1em;
}
.main-header a:hover .logo {
  transform: scale(1.05, 1.05);
}
.main-header a:hover .logo-small {
  transform: scale(1.1, 1.1);
}
.main-header .logo,
.main-header .logo-small {
  transition: ease-in-out 0.1s all;
  vertical-align: middle;
  height: 40px;
}
.main-header .logo-small {
  display: inline-block;
}
.main-header .logo {
  display: none;
}
@media all and (min-width: 768px) {
  .main-header .logo-small {
    display: none;
  }
  .main-header .logo {
    display: inline-block;
  }
}

.main-nav {
  margin: 0 auto;
  text-align: right;
  font-size: 18px;
}
.main-nav ul {
  margin: 0;
}
.main-nav li {
  display: inline;
  margin-right: 20px;
}
.main-nav li:last-child {
  margin-right: 0;
}
.main-nav a {
  display: inline-block;
  font-family: "Varela Round", helvetica, arial, sans-serif;
  text-decoration: none;
}
.main-nav a.active {
  color: #b144ab;
}
.main-nav a.active span::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  display: block;
  margin-top: 4px;
  border-bottom: 2px solid #b144ab;
}
.main-nav a .text {
  position: relative;
  display: none;
}
@media all and (min-width: 480px) {
  .main-nav a .text {
    display: inline;
  }
}
.main-nav a .text,
.main-nav a svg {
  vertical-align: middle;
}
.main-nav svg {
  fill: #0da1b8;
  width: 18px;
  height: 18px;
  margin-right: 10px;
  display: inline-block;
}
.main-nav a.active svg {
  fill: #b144ab;
}